#projects {
  padding: 50px 0 0 0;
  text-align: center;
}
.project-container {
  background-color: #282c34;
}
.project-title {
  padding: 10px 20px;
  color: white;
  margin-bottom: 20px;
}

.project-description {
  padding: 20px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  color: white;
  margin-bottom: 40px;
  max-width: 40%;
  margin: 0 auto;
}
.project-description h2 {
  font-size: 2em;
}
.project-description p {
  font-size: 1.2em;
}
.project-card {
  position: relative;
  margin: 50px auto;
  width: 80%;
  max-width: 500px;
  border: 3px solid #fff;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease-out, box-shadow 0.3s ease-out;
  background-color: white;
  cursor: pointer;
}

.project-image {
  width: 100%;
  height: auto;
  display: block;
  transition: opacity 0.3s ease-out;
}

.project-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.project-overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.654);
  padding: 10px;
  box-sizing: border-box;
  color: white;
  transition: background-color 0.5s linear;
  opacity: 0;
}

.project-card:hover .project-overlay {
  opacity: 1;
}

.project-link {
  color: #61dafb;
  text-decoration: none;
  font-weight: bold;
  margin-bottom: 5px;
  display: block;
}

.project-tech {
  font-size: 0.9em;
  color: #ddd;
}

@media screen and (max-width: 800px) {
  .project-description {
    max-width: 100%;
  }
}
