.about-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 20px;
}

.Presentation {
  font-size: 24px;
  margin-bottom: 10px;
  color: rgb(0, 0, 0);
  font-weight: 500;
}

.my-description{
  margin-top: -9px;

}
.Myname {
  font-size: 24px;
  color: #282c34;
}

.front-end-developer {
  display: inline-block;
  padding: 10px 20px;
  border-radius: 5px;
  margin-top: 10px;
  font-size: 22px;
  font-weight: bold;
  color: white;
  background-color: #61dafb;
  
  transition: none;
 
}

.image-container {
  margin-top: 20px;
}

.img_programmer {
  max-width: 100%;
  height: auto;
}

@media screen and (min-width: 800px) {
  .about-container {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    text-align: left;
  }

  .Presentation {
    font-size: 36px;
    margin-bottom: 0px;
  }

  .Myname {
    font-size: 36px;
    margin-top: 10px;
  }
  .about-container_presentation {
    width: 40%;
  }

  .front-end-developer {
    font-size: 32px;
  }

  .image-container {
    max-width: 50%;
    margin-top: 0;
  }

  .img_programmer {
    margin-left: 0;
  }
}
